const { init: toast } = useToast()

const { isValid, validate } = useForm('addFormRef')

const requiredAddRule = (value) => {
  return !!value?.trim() || 'ËØ•Â≠óÊÆµ‰∏∫ÂøÖÂ°´È°π'
}

const isDeleteConfirmVisible = ref(false)
