  const now = new Date()
  const selected = new Date(value)
  
  // Êó∂Èó¥ÂøÖÈ°ªÊôö‰∫éÂΩìÂâç
  if (selected <= now) return 'Êó∂Èó¥ÂøÖÈ°ªÊôö‰∫éÂΩìÂâç'
  
  return true
}

