const validateVersion = (value) => {
  if (!value) return 'ÁâàÊú¨Âè∑‰∏çËÉΩ‰∏∫Á©∫'  // Ë°•ÂÖÖÁ©∫ÂÄºÂà§Êñ≠
    const pattern = /^v\d+\.\d+\.\d+(-[a-zA-Z0-9]+)?$/
    return pattern.test(value) || 'Ê†ºÂºèÁ§∫‰æãÔºöv1.0.0 Êàñ v2.3.4-beta'
}

// URLÊ†ºÂºèÈ™åËØÅ
const validateURL = (value) => {
