        : text
}

// ‰øÆÊîπ fetchData ÊñπÊ≥ï
const fetchData = async () => {
    loading.value = true
    try {
        const params = {
            page: queryParams.page,
            pagesize: queryParams.pagesize,
			content: searchContent?.value,
			type: selectNotice.value?.value
        }
		
		if (searchSystem) {
			params.system = searchSystem.value?.value
		}

        const res = await noticeList(params)
        // Âõ†‰∏∫Êã¶Êà™Âô®Â∑≤ÁªèÂ§ÑÁêÜ‰∫Ü res.data ÁöÑËß£ÊûÑÔºåËøôÈáåÁõ¥Êé•‰ΩøÁî®ËøîÂõûÂÄº
        if (res?.notices) {
            notices.value = res.notices
            totalItems.value = res.total
        } else {
            notices.value = []
            totalItems.value = 0
        }
    } catch (error) {
        console.error("Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•:", error)
        toast({
            message: error.message || "Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•",
            color: "danger",
        })
        notices.value = []
        totalItems.value = 0
    } finally {
        loading.value = false
    }
}

